.infoWrapper {
    max-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    color: var(--main-color);
    border: var(--main-border);
    box-shadow: 8px 8px 10px #b0a2bd;
    padding: 15px;
}

.infoWrapper > .btn {
    width: 100px;
}

.infoTitle {
    font-size: 3rem;
}

.infoText {
    font-size: 2rem;
    text-align: center;
    line-height: 3rem;
}

.infoLink {
    transition: transform 0.5s ease;
    font-weight: 600;
}

.infoLink:hover {
    transform: scale(1.2);
}
